/** @jsx jsx */
import { jsx } from "theme-ui";
import { graphql } from "gatsby";
import CollectionListingContainer from "@components/CollectionListingContainer";
import SEO from "@components/seo";
import React from "react";
const PopulationGroups = (props) => {
  const { category } = props.data.craft;

  return (
    <React.Fragment>
      <SEO
        metadata={{
          metaTitle: category.title,
        }}
        location={props.location}
      />
      <CollectionListingContainer
        category={category}
        listing="populationGroups"
      />
    </React.Fragment>
  );
};

export default PopulationGroups;

export const query = graphql`
  query PopulationGroupsQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_populationGroups_Category {
          title
        }
      }
    }
  }
`;
